@media print and (color) {
    * {
        -webkit-print-color-adjust: exact;
        print-color-adjust: exact;
    }
}

@media print {
    body {
        margin: 0;
    }

    .print-table {
        display: table;
    }

    a {
        text-decoration: none !important;
        color: inherit !important;
    }

    .header_name {
        font-size: 33px;
        text-align: right;
    }

    .header_other {
        font-size: 15px;
        text-align: right;
    }

    #contentbar {
        width: 65%;
        display: table-cell;
    }

    #sidebar {
        width: 35%;
        display: table-cell;
    }

    .headerCircle {
        display: -ms-flexbox;
        display: flex;
    }

    .headerBox {
        margin-left: 5%;
        margin-right: 5%;
        width: 90%;
        position: absolute;
        top: 50px;
    }

    .headerText {
        text-align: right;
        margin-right: 10px;
    }

    .sectionContainer {
        margin: 0 10px 0 10px;
    }

    .xbutton {
        display: none !important;
    }

    a.pseudo-button:after {
        content: ": (" attr(href) ")";
    }

    a.pseudo-button {
        text-decoration: none !important;
        color: black;
        display: inline;
        border: none;
        -webkit-box-shadow: none;
                box-shadow: none;
        padding: 0;
        background: none !important;
    }

}




